import React from "react";

import { PaginationWrapper } from "./styled.js";

const Pagination = ({ totalPages, changePage, page, showElements = 3 }) => {
    const pagesList = [...new Array(totalPages)].map((_, i) => i + 1);

    const changeBack = () => page > 1 && changePage(page - 1);
    const changeNext = () => totalPages - page > 0 && changePage(page + 1);

    return (
        <PaginationWrapper>
            {totalPages > 1 && (
                <div className={`prevNext ${page === 1 ? "disabled" : ""}`} onClick={changeBack}>
                    prev
                </div>
            )}
            <div
                className={`page ${page > showElements + 2 ? "withDotsRight" : ""} ${
                    page === 1 ? "currentPage" : ""
                }`}
                onClick={() => changePage(1)}
            >
                1
            </div>
            {pagesList
                .slice(1, -1)
                .filter((p) => p - page <= showElements && page - p < showElements + 1)
                .map((pageNum) => (
                    <div
                        className={`page ${page === pageNum ? "currentPage" : ""}`}
                        key={pageNum}
                        onClick={() => changePage(pageNum)}
                    >
                        {pageNum}
                    </div>
                ))}

            {totalPages > 1 && (
                <>
                    <div
                        className={`page ${page < totalPages - (showElements + 1) ? "withDotsLeft" : ""} ${
                            page === totalPages ? "currentPage" : ""
                        }`}
                        onClick={() => changePage(totalPages)}
                    >
                        {totalPages}
                    </div>
                    <div className={`prevNext ${page === totalPages ? "disabled" : ""}`} onClick={changeNext}>
                        next
                    </div>
                </>
            )}
        </PaginationWrapper>
    );
};

export default Pagination;
